@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,500;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alatsi&family=Kumbh+Sans:wght@300;400;700&family=Passion+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  list-style: none;

}
body{
  background-color: #ffffff;
}

body {
  overflow-y: hidden;
}

.phoneImage {
  position: absolute;
  left: 50%;
  top: 8%;
  transform: translateX(-50%);
  width: 100%;
  height: 50%;
}

.phoneImage .explore {
  position: absolute;
  z-index: 9;
  left: 9%;
  top: 54%;
  width: 10%;
  height: 40%;
}

.phoneImage .explore h1 {
  color: white;
  font-size: 60px;
  text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.12);
}

.phoneImage .explore button {
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  padding-left: 40px !important;
  padding-right: 40px !important;
  background-color: #161616 !important;
  border-radius: 0 !important;
  font-family: "Raleway", sans-serif;
  font-weight: 900;
}

.phoneImage img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  height: 800px;
  object-fit: cover;
  border-radius: 10px;
}
/*# sourceMappingURL=Home.css.map */
nav .logo {
  position: absolute;
  top: 2.4%;
  left: 5%;
}

nav .logo a:hover {
  text-decoration: none !important;
}

nav .logo h1 {
  color: #292929;
  text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.12);
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  font-size: 30px;
}

nav .logo h1:hover {
  cursor: pointer;
}

nav .navlinks {
  display: flex;
  position: absolute;
  top: 3%;
  left: 50%;
  width: 18%;
  justify-content: space-around;
  transform: translateX(-50%);
}

nav .navlinks li a {
  font-size: 19px;
  color: #292929;
  font-family: 'Alatsi', sans-serif;
  font-weight: 400;
  text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.12);
}

nav .navlinks li a:hover {
  text-decoration: none;
}
/*# sourceMappingURL=Navbar.css.map */
